import { Component } from '@angular/core';
import { Article } from './article/article.model';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})


export class AppComponent {
  
  articles : Article[]
  filterValue : any


  constructor(){

      this.articles = [
        new Article('Roxo Online Tutorials', 'sdfsfsdf sdfsdfsdfsdfsdfsd', "image1"),
        new Article('Roxo Online Tutorials22', 'sdfsfsdxcvxcvsdfsdfsdfsd', "image2"),
        new Article('Roxo Online Tutorials333', 'sdfsfsdf sdfsdfcxvcxvd', "image3")
      
      ];

  }

  
  FilterData(){
    if(!this.filterValue) return this.articles
    return this.articles.filter(art => art.title.indexOf(this.filterValue) != -1)
  }

  addArticle(title:HTMLInputElement, des:HTMLTextAreaElement, image:HTMLInputElement){
      this.articles.unshift(new Article(title.value, des.value, image.value))
  }



}



